import Vue from 'vue'
import Router from 'vue-router'
import Hello from '../components/Hello'
import Vipzone from '../components/vipzone'

Vue.use(Router)

export default new Router({
  mode: 'history',		// 更改为history模式
  base: '/vip/added/vipzoneNew',
  routes: [
    {
		path: '/index.html',
		name: 'Vipzone',
		component: Vipzone
    },
    { 
		path: '*', 
		redirect: '/index.html' 
    }
  ]
})




